.nav-bar {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 32px;
    z-index: 10;
    display: flex;
    box-shadow: 0 4px 4px 0 rgba($color: #000000, $alpha: 0.25);
    border-radius: 40px;
    background-color: #e7e4db;

    .content-left, .content-right {
        background-color: #e7e4db;
        display: flex;
        flex-direction: column;
        justify-content: center;
        transition: background-color 0.4s;
        background-color: url("../../assets/bg-img.png");
        background-size: cover;

        img {
            width: 20px;
            height: 20px;
            margin: auto;
            padding: 10px 20px;
        }
    }

    .content-left {
        border-top-left-radius: 40px;
        border-bottom-left-radius: 40px;
    }
    
    .content-right {
        border-top-right-radius: 40px;
        border-bottom-right-radius: 40px;
    }

    .content {
        background-color: #272727;
        display: flex;
        padding: 0 20px;

        .link {
            font-size: 16px;
            background-color: #272727;
            color: #ddd;
            font-weight: bold;
            padding: 18px;
            cursor: pointer;
            margin: auto;
            text-align: center;
            transition: color 0.4s;

             &:hover {
                color: white;
             }
        }
    }
}

.hidden {
    opacity: 0;
}

.show {
    opacity: 1;
}

.active {
    cursor: pointer;

    &:hover {
        background-color: #EDAAAF;
    }
}